import React, { useState } from 'react'
import './Card.css'
import { Favorite, FavoriteBorder } from '@mui/icons-material';

const Card = ({name,imgUrl,price1,price2,id}) => {
    const [hart, setHart] = useState(false);



const changeHart = (id) =>{
  setHart(!hart)
}





  return (
    <div className="card " style={{ border: "1px solid #20e062" }}>
      <div className="like_box">
        <i onClick={() => changeHart(id)}>
          {" "}
          {hart ? <Favorite /> : <FavoriteBorder />}{" "}
        </i>
        <h4 className="">20% sale</h4>
      </div>
      <img src={imgUrl} alt="" />
      <h6>{name}</h6>
      <p>
        <span>{price1}.00</span>{" "}
        <span className="text-secondary">{price2}.00</span>
      </p>

      <a type="button" className="button  rounded">
        <h5>Add To Cart</h5>
        <i>@</i>
      </a>
    </div>
  );
}

export default Card