import React from 'react'
import './Contact.css'

const Contact = () => {
  return (
    <div>


      <div className="wrapper px-sm-0 px-3" id='Contact'>
			<div className="inner">
				<form action="">
					<h3 className='mb-4'>Contact Us</h3>
					{/* <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore.</p> */}
					<label className="form-group">
						<input type="text" className="form-control"  required />
						<span className='text-up'>Your Name</span>
						<span className="border"></span>
					</label>
					<label className="form-group">
						<input type="text" className="form-control"  required />
						<span for="" className='text-up'>Your Mail</span>
						<span className="border"></span>
					</label>
					<label className="form-group" >
						<textarea name="" id="" className="form-control" required></textarea>
						<span for="" className='text-up'>Your Message</span>
						<span className="border"></span>
					</label>
					<button>Submit 
						<i className="zmdi zmdi-arrow-right"></i>
					</button>
				</form>
			</div>
		</div>


    </div>
  )
}

export default Contact