import React, { useState } from "react";
import { AnimationOnScroll } from "react-animation-on-scroll";
import "animate.css/animate.min.css";
import "./Foods.css";
import img1 from "../../images/1.png";
import img2 from "../../images/2.png";
import img3 from "../../images/3.png";
import img4 from "../../images/4.png";
import img5 from "../../images/5.png";
import img6 from "../../images/6.png";
import img7 from "../../images/7.png";

const Foods = () => {
  const[btn_show, setBtn_show] = useState(true)
    const [addData, setAddData] = useState([
      {
        backgrlund_img: "https://cdn.wallpapersafari.com/46/3/6OjDmk.jpg",
        title: "Kung Pao Pork",
        content:
          "Kung Pao Pork is a variation Kung Pao chicken, which is a delicious dish that I always enjoy in Chinese take-outs – especially if I have it with Yang Chow fried rice. This dish is a bit spicy, but very tasty. The addition of zucchini and bell pepper balanced the flavor of this dish and makes the texture even better.",
        img: img3,
        id: 3,
      },
      {
        backgrlund_img: "https://cdn.wallpapersafari.com/56/64/Z3C1wf.jpg",
        title: "Indian Samosa ",
        content:
          "A samosa is a fried or baked pastry with a savory filling, including ingredients such as spiced potatoes, onions, peas. It may take different forms, including triangular, cone, or half-moon shapes, depending on the region. Samosas are often accompanied by chutney, and have origins in medieval times or earlier. Samosas are a popular entrée, appetizer, or snack in the cuisines of South Asia, the Middle East, Central Asia, East Africa and their diasporas.",
        img: img4,
        id: 4,
      },
      {
        backgrlund_img: "https://cdn.wallpapersafari.com/75/15/CQsSX7.jpg",
        title: "Afghani Kabuli Pulao",
        content: `When you don’t know what dish to cook with leftover lamb, consider Kabuli Pulao, or Afghan Rice With Lamb. It’s a tasty and hearty dish that will make you swoon with the very fist bite.

You can find this dish with the name “qabli palaw”, and the word 'qabli ' means 'able'. It has that name because there is a fascinating traditional belief behind the dish.`,
        img: img5,
        id: 5,
      },
      {
        backgrlund_img:
          "https://www.helpguide.org/wp-content/uploads/hands-holding-veges-1536.jpg",
        title: "Pakistani Cuisine",
        content: `As I exited the car, I could smell the fragrant chicken being cooked on barbeque like pits. I could see the smoke rise up into the sky. As we arrived to our seat, I thought to myself, 'This place looks ghetto!' Yes, the food smelt good, but being outside and eating on dirty tables, which stood on top of dirt, didn't help my appetite. This made me sort of nervous about my first meal out in Pakistan. All this time`,
        img: img6,
        id: 6,
      },
    ]);
    const[width,setWidth] = useState(window.innerWidth)
   
  const [data, setData] = useState([
    {
      backgrlund_img:
        "https://i.insider.com/5c17c01f01c0ea0d092afae3?width=1300&format=jpeg&auto=webp",
      title: "Spain Paella Food",
      content:
        "paella, in Spanish cuisine, a dish of saffron-flavoured rice cooked with meats, seafood, and vegetables. Originating in the rice-growing areas on Spain’s Mediterranean coast, the dish is especially associated with the region of Valencia. Paella takes its name from the paellera, the utensil in which it is cooked, a flat round pan with two handles; paella is traditionally eaten from the pan.",
      img: img1,
      id: 1,
    },
    {
      backgrlund_img:
        "https://i.insider.com/5ae8aa13bd9671f8008b46c3?width=900&format=jpeg&auto=webp",
      title: "Italyan Pasta Food",
      content:
        "Pasta is made from grain, one of the basic food groups in a healthy diet that also can include vegetables, fruits, fish, and poultry. It’s a good source of energy and can give you fiber, too, if it’s made from whole grain. That can help with stomach problems and may help lower cholesterol. Try some tagliatelle with wild mushrooms and truffle oil.",
      img: img2,
      id: 2,
    },
  ]);
  setInterval(() => {
      setWidth(window.innerWidth)
  }, 10);
 const inc_item = () => {
     setData([
         ...data,
         ...addData
     ])
     setAddData(null)
     setBtn_show(false)
 };
  return (
    <div className="foods" id="Foods">
      <div className="foods_title_box">
        <div className="food_title_chield_1 d-md-block d-none">
          <img src="https://cdn.wallpapersafari.com/62/71/Cij1ya.jpg" alt="" />
        </div>
        <div className="food_title_chield_2">
          <span>THE MOST FAMOUS </span>
          <h2>FOOD AROUND THE WORLD</h2>
        </div>
        <div className="food_title_chield_3  d-md-block d-none">
          <img
            src="https://www.pngall.com/wp-content/uploads/8/Fried-Chinese-Spring-Roll-PNG-Picture.png"
            alt=""
          />
        </div>
      </div>
      <div className="foods_item_box">
        {data.map((item, index) => (
          <>
            {width > 730 ? (
              <>
                {item.id % 2 === 0 ? (
                  <>
                    <div
                      className="food_item mt-4 "
                      style={{
                        background: `url(${item.backgrlund_img}),rgba(0, 0, 0, 0.369)`,
                      }}
                    >
                      <div className="img_box pb-3">
                        <img src={item.img} alt="" />
                      </div>
                      <div className="text_box">
                        <AnimationOnScroll
                          animatePreScroll={false}
                          duration={5}
                          animateIn="hinge"
                          initiallyVisible={true}
                          delay={700}
                        >
                          <h3 className="text-md-start text-center">
                            {item.title}
                          </h3>
                          <p className="text-md-start  text-center">
                            {item.content}
                          </p>
                          <a href="" className="shadow bg-light">
                            Read More
                          </a>
                        </AnimationOnScroll>
                      </div>
                    </div>
                  </>
                ) : (
                  <>
                    <div
                      className="food_item mt-4"
                      style={{
                        background: `url(${item.backgrlund_img}),rgba(0, 0, 0, 0.369)`,
                      }}
                    >
                      <div className="text_box">
                        <AnimationOnScroll duration={5} animateIn="hinge">
                          <h3 className="text-md-start text-center">
                            {item.title}
                          </h3>

                          <p className="text-md-start text-center">
                            {item.content}
                          </p>

                          <a href="" className="shadow bg-light">
                            Read More
                          </a>
                        </AnimationOnScroll>
                      </div>
                      <div className="img_box">
                        <img src={item.img} alt="" />
                      </div>
                    </div>
                  </>
                )}
              </>
            ) : width < 730 ? (
              <>
                <div
                  className={`food_item mt-4 py-3   ${
                    width > 550 ? "px-5" : ""
                  }`}
                  style={{
                    background: `url(${item.backgrlund_img}),rgba(0, 0, 0, 0.369)`,
                  }}
                >
                  <div className={`img_box ${item.id === 3 ? "mb-4" : ""}`}>
                    <img src={item.img} />
                  </div>
                  <div className="text_box">
                    <AnimationOnScroll
                      animateIn="flipInY"
                      animateOut="flipOutY"
                      duration={1}
                    >
                      <h3>{item.title}</h3>
                      <p>{item.content}</p>
                      <a href="" className="shadow bg-light">
                        Read More
                      </a>
                    </AnimationOnScroll>
                  </div>
                </div>
              </>
            ) : null}
          </>
        ))}

        <div class="d-grid gap-2 col-6 mx-auto">
          <button
            class={`btn btn-success my-5 ${btn_show ? "" : "d-none"} c_bg_color`}
            onClick={() => inc_item()}
            type="button"
          >
            View More
          </button>
        </div>
      </div>
    </div>
  );
};

export default Foods;
