import React from 'react'
import './Owl_C_Part.css'

const Owl_C_Part = ({bg}) => {
  return (
    <> 
      
        <div className="owl_card pb-5">
            <img src={bg} alt=''/>
            <h3 className='p4'><a href="">SEARCH MORE ABOUT THE FOODS</a></h3>
            <p className=''>What do you call a sad strawberry?</p>
        </div>
      
    </>
  )
}

export default Owl_C_Part