import React from 'react'
// import Carousel from '../../parts/SliderPage/Carousel.jsx'
import Navbar from '../../parts/Navbar/Navbar'
import './Home.css'
import mango from '../../images/mango.png'
import H_btn from '../../parts/H_btn/H_btn'

const Home = () => {
  return (
    <div className="Home" id="Home">
      <div className="nav_bar">
        <Navbar />
      </div>
      <div className="home_content">
        <div className="home_inner_content">
          <h2
            id="animation_heading"
            className="pt-md-4 mb-0 text-sm-start text-center"
          >
            Eat less from the box, more from the earth.
          </h2>
          <p
            id="animation_content "
            className="pt-md-4 mb-0 text-sm-start text-center"
          >
            Part of secret to success in life is to eat what you like and let
            the food fight it out inside. The people who love to eat are always
            the best people.
          </p>
          <H_btn />
        </div>
        <div className="home_inner_img">
          <img src={mango} className="mango" alt="" />
        </div>
      </div>
    </div>
  );
}

export default Home





