import React from 'react'
import logo from '../../images/logo.png'
import './Navbar.css'
const Navbar = () => {
  return (
    <div className="px-sm-5 px-3 d-flex align-items-center a1 Home_page container-fluid">
      <nav className="navbar navbar-expand-lg navbar-light">
        <div className="container-fluid">
          <a className="navbar-brand" href="#">
            
            <img
              src={logo}
              alt="this slowpoke moves"
              width="250"
              style={{ width: "70px", height: "60px", borderRadius: "50px" }}
            />
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav m-auto">
              <li className="nav-item mx-sm-4 mx-2">
                <a className="nav-link active" aria-current="page" href="#Home">
                  Home
                </a>
              </li>
              <li className="nav-item mx-sm-4 mx-2">
                <a className="nav-link c_color" href="#Foods">
                  Foods
                </a>
              </li>
              <li className="nav-item mx-sm-4 mx-2">
                <a className="nav-link c_color" href="#Carousel">
                  Carousel
                </a>
              </li>
              <li className="nav-item mx-sm-4 mx-2">
                <a className="nav-link c_color" href="#Contact">
                  Contact Us
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default Navbar