import chips_img from '../../images/chips_eating.png'
import "animate.css/animate.min.css";
import {AnimationOnScroll} from "react-animation-on-scroll";
import './Chips_box.css'

const Chips_box = () => {
  return (
    <div className="chips_box">
      <div className="text_box">
        <AnimationOnScroll
          animateIn="bounceInRight"
          animateOut="bounceOutLeft"
          duration={0.7}
        >
          <h2>What Is Emotional Eating?</h2>
        </AnimationOnScroll>
        <AnimationOnScroll
          duration={0.7}
          animateIn="fadeIn"
          animateOut="fadeOut"
        >
          <p className='px-sm-0 px-3'>
            Emotional eating is when people use food as a way to deal with
            feelings instead of to satisfy hunger. We've all been there,
            finishing a whole bag of chips out of boredom or downing cookie
            after cookie while cramming for a big test. But when done a lot —
            especially without realizing it — emotional eating can affect
            weight, health, and overall well-being.
          </p>
        </AnimationOnScroll>
      </div>
      <div className="img_box ps-sm-5">
        <img src={chips_img} alt="" />
      </div>
    </div>
  );
}

export default Chips_box