import React from 'react'
import './F_btn.scss'
const F_btn = () => {
  return (
    <a className="f_btn" href='#Home' >
      <div id="container">
        <button class="learn-more">
          <span class="circle" aria-hidden="true">
            <span class="icon arrow"></span>
          </span>
          <span class="button-text">Go To Top</span>
        </button>
      </div>
    </a>
  );
}

export default F_btn